@use "@app/bancor-components/Container/BaseExternal";
@use "@app/bancor-components/Style/screen";
@use "@app/bancor-components/Style/size";
@use "@app/bancor-components/Element/Button";
@use "@app/bancor-components/Element/Text";
@use "@app/bancor-components/Element/Link";
@use "@app/bancor-components/Style/Color";
@use "@app/bancor-components/Style/Font";
@use "@app/bancor-components/Style/Border";
@use "@app/bancor-components/Style/Layout";
@use "@app/bancor-components/Style/Layout/Flex";
@use "@app/bancor-components/Style/Layout/Margin";
@use "@app/bancor-components/Style/Layout/Padding";
@use "@app/bancor-components/Style/Layout/Display";
@use "@app/bancor-components/Element/Form/InputPassword";
@use "@app/bancor-components/Element/Image";

$login-title-step1-mobile: 207px;
$login-title-step1-desktop: 500px;
$eo-web-width: 232px;
$eo-web-height: 192px;
$eo-mobile-width: 202px;
$eo-mobile-height: 141px;

#{BaseExternal.$selector} {
    @include screen.lg {
        &.containerStep3 {
            overflow-y: initial;
        }
    }

    .logoLogin {
        @include Margin.set($bottom: delta);

        #{Image.$selector} {
            width: $eo-mobile-width;
            height: $eo-mobile-height;

            img {
                object-fit: contain;
            }
        }
    }

    .utilTools {
        @include Flex.set($justify: center);

        #{Button.$selector} {
            @include Padding.set($preset: none);
            @include Color.set($bg: "transparent");
            @include Border.set($style: default);

            #{Text.$selector} {
                @include Color.set($fg: "primary");
                @include Font.set($size: gamma, $weight: medium, $family: "default", $align: default);
            }
        }
    }

    .separator {
        @include Margin.set($top: eta);
        @include Display.set($display: none);

        #{Text.$selector} {
            @include Color.set($fg: "gray-dark");
            @include Font.set($size: gamma, $weight: default, $family: "heading", $align: default);
        }
    }

    .bottomlessButton {
        @include Display.set($display: none);
    }

    .alignRecommendations {
        @include Flex.set($justify: center, $align: center);
        @include Margin.set($bottom: alpha);

        #{Link.$selector} {
            @include Font.set($deco: default);

            #{Text.$selector} {
                @include Color.set($fg: "primary");
                @include Font.set($size: gamma, $weight: medium, $family: "default", $align: default);
            }
        }
    }

    .securitySeal {
        user-select: none;
        @include Flex.set($dir: column, $align: center);
        @include Margin.set($bottom: delta);

        & > :first-child {
            @include Margin.set($bottom: alpha);
        }
    }
    .greeting {
        @include Flex.set($align: center);
        #{Text.$selector} {
            @include Color.set($fg: "gray-dark");
            @include Font.set($size: kappa, $weight: medium, $family: "heading");
            @include Padding.set($left: alpha, $right: alpha);
        }
    }

    .selectOperation {
        @include Margin.set($bottom: epsilon);
        @include Flex.set($justify: center, $align: center);

        #{Text.$selector} {
            @include Color.set($fg: "gray-dark");
            @include Font.set($size: gamma, $weight: default, $family: "default", $align: default);
        }
    }

    .userRecovery,
    .passwordRecovery {
        @include Margin.set($top: beta, $bottom: beta);
        @include Padding.set($right: alpha);
        @include Flex.set($justify: end);

        #{Link.$selector} {
            @include Font.set($deco: default);

            #{Text.$selector} {
                @include Color.set($fg: "gray-light");
                @include Font.set($size: gamma, $weight: medium, $family: "default", $align: right);
            }
        }
        @include screen.lg {
            @include Margin.set($bottom: gamma);
        }
    }

    .biometrySession {
        @include Margin.set($top: epsilon);
        @include Flex.set($justify: center, $align: center);

        & > svg {
            @include Margin.set($right: alpha);
            height: Layout.get-size(beta);
            width: Layout.get-size(beta);
        }

        #{Button.$selector} {
            @include Padding.set($preset: none);
            @include Color.set($bg: "transparent");
            @include Border.set($style: default);

            #{Text.$selector} {
                @include Color.set($fg: "primary");
                @include Font.set($size: gamma, $weight: medium, $family: "default", $align: default);
            }
            &:disabled #{Text.$selector} {
                @include Color.set($fg: "gray-600");
            }
        }
    }

    .biometryAvailable {
        @include Flex.set($justify: start);
        @include Margin.set($bottom: delta);
        &.captchaEnabled {
            @include Margin.set($top: gamma);
        }
    }

    .createToken {
        @include Margin.set($bottom: delta, $top: delta);
        @include Flex.set($justify: center, $align: center);

        & > svg {
            @include Margin.set($right: alpha);
            height: Layout.get-size(beta);
            width: Layout.get-size(beta);
        }

        #{Link.$selector} {
            @include Font.set($deco: default);

            #{Text.$selector} {
                @include Color.set($fg: "primary");
                @include Font.set($size: gamma, $weight: medium, $family: "default", $align: default);
            }
        }
    }

    .environments {
        @include Margin.set($bottom: epsilon);
    }

    .alignInputs {
        @include Margin.set($top: delta);
        #{InputPassword.$selector} {
            @include Margin.set($bottom: delta);
        }
    }

    .expiredPassword {
        @include Margin.set($top: beta);
        @include Flex.set($justify: center, $align: center);
    }

    .stepBlockedUserByPassword {
        @include Flex.set($dir: column);

        & > header #{Text.$selector} {
            @include Font.set($size: iota, $weight: bold, $family: "heading", $align: default);
            @include Margin.set($bottom: beta, $top: beta);
        }

        & > section #{Text.$selector} {
            @include Font.set($size: gamma, $weight: normal, $family: "default", $align: default);
            @include Margin.set($bottom: delta);
        }
        & > footer #{Button.$selector} {
            @include Margin.set($bottom: beta);
        }
    }

    .modalWelcome {
        > div {
            > div:first-child {
                @include Flex.set($justify: center);
                @include Padding.set($bottom: none);
                @include Margin.set($bottom: beta);

                @include screen.lg {
                    @include Flex.set($justify: between);
                }
            }

            .modalUList {
                li {
                    @include Flex.set($dir: default);

                    svg {
                        @include Flex.set($shrink: none);
                        @include Margin.set($right: beta);
                    }
                }
            }
        }

        @include screen.lg {
            .modalWelcomeButton {
                @include Margin.set($left: auto);
            }
        }
    }

    #{Text.$selector}.welcomeText {
        @include Font.set($size: delta, $weight: default, $family: "default");
    }

    @include screen.lg {
        .logoLogin {
            @include Margin.set($bottom: epsilon);

            #{Image.$selector} {
                width: $eo-web-width;
                height: $eo-web-height;
            }
        }

        .selectOperation {
            @include Margin.set($top: none, $bottom: delta);
        }

        .alignRecommendations {
            @include Margin.set($top: delta);
        }

        .createToken,
        .utilTools,
        .alignFooter {
            @include Display.set($display: none);
        }

        .separator,
        .bottomlessButton {
            @include Display.set($display: block);
        }

        .environments {
            @include Margin.set($bottom: kappa);
        }
    }

    .updateModal {
        .footer {
            @include Flex.set($dir: column, $align: center, $justify: flex-end);
            width: Layout.get-max-width(alpha);

            #{Button.$selector} {
                width: 100%;
            }
            #{Button.$selector}:last-child {
                @include Margin.set($top: beta);
            }
        }
    }
    
    
}
